import React from 'react';

class UserInput extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            screen_name: ""
        }
    }

    handleChange = (e) => {
        this.setState({
            screen_name: e.target.value
        })
    }

    handleAdd = (e) => {
        e.preventDefault()
        console.log(this.state.screen_name)
        this.props.sendUser(this.state.screen_name)
        // isn't working, need to make input value = this most likely
        // this.setState({
        //     screen_name: ""
        // })
    }

    render() {
        if (this.props.loading) {
            return (
                <form className="form-inline justify-content-between my-2">
                    <div className="form-group mb-3 mx-1">
                        <label className="sr-only">Twitter Handle</label>
                        <input 
                            onChange={this.handleChange} 
                            type="text" 
                            className="form-control" 
                            placeholder="Twitter Handle" />
                    </div>
                    <i className="fas fa-spinner fa-spin fa-2x" style={{verticalAlign: "middle"}}></i>
                </form>
            )
        }
        return (
            <form className="form-inline justify-content-between my-2">
                <div className="form-group mb-3 mx-1">
                    <label className="sr-only">Twitter Handle</label>
                    <input 
                        onChange={this.handleChange} 
                        type="text" 
                        className="form-control" 
                        placeholder="Twitter Handle" />
                </div>
                <button onClick={this.handleAdd} className="btn btn-outline-info mb-3 mx-1">add user</button>
            </form>
        )
    }
}

export default UserInput;