import React from 'react';

class Prediction extends React.Component {
    constructor(props){
        super(props)
        this.state = {
            input: ''
        }
    }

    handleChange = (e) => {
        this.setState({
            input: e.target.value
        })
    }

    handleSubmit = (e) => {
        e.preventDefault() 
        this.props.makePrediction(this.state.input)
    }

    render() {
        var resultStatement;
        if (this.props.prediction) {
            resultStatement = (
                <h2 className="text-dark my-2">That has to be {this.props.prediction}!</h2>
            )
        } else {
            resultStatement = (
                <div></div>
            )
        }
        console.log("PREDICTION PROPS")
        console.log(this.props)
        if (this.props.loading) {
            return (
                <div>
                    <div className="form-group">
                        <label>example tweet</label>
                        <textarea 
                            className="form-control" 
                            rows="3"
                            onChange={this.handleChange}></textarea>
                    </div>
                    <i className="fas fa-spinner fa-spin fa-2x"></i>
                </div>
            ) 
        }
        return (
            <div>
                <div className="form-group">
                    <label>example tweet</label>
                    <textarea 
                        className="form-control" 
                        rows="3"
                        onChange={this.handleChange}></textarea>
                </div>
                <button 
                    onClick={this.handleSubmit} 
                    className="btn btn-outline-info">get prediction</button>
                {resultStatement}
            </div>
        )
    }
}

export default Prediction;