import React from 'react';
import Navbar from './Navbar/Navbar';
import UserList from './UserList/UserList'
import UserInput from './UserInput/UserInput';
import Prediction from './Prediction/Prediction';
import { getUsers, addUser, makePrediction } from '../util';
import './App.css';

class App extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      users: [],
      id_one: null,
      id_two: null,
      prediction: null,
      inputLoading: false,
      predictionLoading: false
    }
  }

  componentDidMount() {
    getUsers()
      .then((users) => {
        console.log(users)
        this.setState({
          users: users
        })
      })
      .catch((error) => {
        console.log("ERROR:")
        console.log(error)
      })
  }

  addUser = (screen_name) => {
    this.setState({
      inputLoading: true
    })
    addUser(screen_name)
      .then((user) => {
        console.log(user)
        let newUsers = this.state.users;
        newUsers.push(user)
        this.setState({
          users: newUsers,
          inputLoading: false
        })
      })
      .catch(error => {
        console.log('Error adding')
        console.log(error)
        this.setState({
          inputLoading: false
        })
      })
  }

  makeActive = (e) => {
    this.setState({
      id_one: e.target.value,
      id_two: this.state.id_one
    })
  }

  makePrediction = (sentence) => {
    if (this.state.id_one === null || this.state.id_two === null) {
      alert("Must select two users for model to choose from.")
    } else {
      this.setState({
        predictionLoading: true
      })
      makePrediction(this.state.id_one, this.state.id_two, sentence)
        .then((result) => {
          console.log(result)
          this.setState({
            prediction: result,
            predictionLoading: false
          })
        })
        .catch((error) => {
          console.log(error)
          this.setState({
            predictionLoading: false
          })
        })
    }
  }

  render() {
    return (
      <div className="App bg-light vh-100">
        <Navbar />
        <div className="row mx-1">
          <div className="col">
            <UserInput sendUser={this.addUser} loading={this.state.inputLoading} />
            <UserList 
              users={this.state.users}
              id_one={this.state.id_one}
              id_two={this.state.id_two} 
              makeActive={this.makeActive}/>
          </div>
          <div className="col">
            <Prediction 
              makePrediction={this.makePrediction}
              prediction={this.state.prediction}
              loading={this.state.predictionLoading} />
          </div>
        </div>
      </div>
    );
  }
}

export default App;
