const API_URL = "https://twit-off-bendevera.herokuapp.com"

export const getUsers = function(){
    return new Promise((resolve, reject) => {
        console.log(API_URL + "/api/users")
        fetch(API_URL + "/api/users")
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson['data'])
        })
        .catch((error) => {
            reject(error)
        })
    })
}

export const addUser = function(screen_name){
    return new Promise((resolve, reject) => {
        console.log(API_URL + "/api/users")
        fetch(API_URL + "/api/users", {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({screen_name: screen_name})
        })
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson['data'])
        })
        .catch((error) => {
            reject(error)
        })
    })
}

export const makePrediction = function(id_one, id_two, exampleTweet){
    return new Promise((resolve, reject) => {
        console.log(API_URL + "/api/predict")
        fetch(API_URL + "/api/predict", {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                id_one: id_one,
                id_two: id_two,
                sentence: exampleTweet
            })
        })
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson['data'])
        })
        .catch((error) => {
            reject(error)
        })
    })
}